export default {
	props: {
		item: Object,
		index: Number
	},
	data() {
		return {
			infonum:this.item.infonum,
			isguanzhu:this.item.isguanzhu
		};
	},
	onLoad() {},
	methods: {
		guanzhu() {
			this.isguanzhu = true;
			uni.showToast({
				title: '关注成功！'
			});
		},
		//操作
		caozuo(type) {
			// console.log('点击了');
			//不能直接修改父组件传过来的值
			switch (type) {
				case 'ding':
					if (this.infonum.index == 1) {
						return;
					}
					this.infonum.dingnum++;
					if (this.infonum.index == 2) {
						this.infonum.cainum--;
					}
					this.infonum.index = 1;
					break;
				case 'cai':
					if (this.infonum.index == 2) {
						return;
					}
					this.infonum.cainum++;
					if (this.infonum.index == 1) {
						this.infonum.dingnum--;
					}
					this.infonum.index = 2;
					break;
			}
		},
		//进入详情页
		opendetail(){
			// console.log('进入详情页')
			// this.$emit('opendetail',this.item)
			uni.navigateTo({
				url:"/pages/detail/detail?detailData="+JSON.stringify(this.item)
			})
		},

		
	}
};
